/* src/App.css */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #000; /* Initial dark blue color */
  color: #fff; /* White text */
  animation: breathingBackground 5s infinite alternate; /* Faster breathing effect animation */
}

@keyframes breathingBackground {
  0% {
    background-color: #001f3f; /* Darker blue */
  }
  100% {
    background-color: #000033; /* Slightly lighter blue */
  }
}

.instruction-text {
  font-size: 1.5rem; /* Adjust the font size as desired */
}


.App {
  text-align: center;
}

.container {
  max-width: 90%; /* Adjust maximum width */
  margin: 0 auto;
  padding: 40px 20px; /* Adjust padding */
}

.logo-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3vw; /* Adjust margin with viewport width */
}

.logo {
  height: 8vw; /* Adjust logo size with viewport width */
}

.other-logos {
  display: flex;
}

.content {
  position: relative;
}

.headline {
  font-size: 3.5vw; /* Adjust font size with viewport width */
  line-height: 1.5;
  margin-bottom: 3vw; /* Adjust margin with viewport width */
  opacity: 0;
  animation: fadeIn 3s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#start-btn {
  background-color: #007bff; /* Blue button */
  color: #fff;
  border: none;
  padding: 1.5vw 3vw; /* Adjust padding with viewport width */
  font-size: 2vw; /* Adjust font size with viewport width */
  cursor: pointer;
  border-radius: 1vw; /* Adjust border radius with viewport width */
  transition: background-color 0.3s ease;
  opacity: 0;
  animation: fadeInBtn 3s forwards;
}

#start-btn:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

@keyframes fadeInBtn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 20px; /* Adjust padding for smaller screens */
  }

  .logo-container {
    flex-direction: column;
    margin-bottom: 2vw; /* Adjust margin with viewport width */
  }

  .logo {
    height: 10vw; /* Adjust logo size with viewport width */
    margin-bottom: 2vw; /* Adjust margin with viewport width */
  }

  .headline {
    font-size: 4vw; /* Adjust font size with viewport width */
    margin-bottom: 2vw; /* Adjust margin with viewport width */
  }

  #start-btn {
    padding: 2vw 4vw; /* Adjust padding with viewport width */
    font-size: 3vw; /* Adjust font size with viewport width */
    border-radius: 1.5vw; /* Adjust border radius with viewport width */
  }
}
